import { MobileToolbarItem } from './types';

export const BOTTOM_BAR_SIZE = 44;
export const BOTOTM_PANEL_HEIGHT = 400;

export const Z_INDEX = 1900;

export const PANEL_HEADER_SIZE = 56;

// icon 类的 button 宽度
export const ICON_BUTOTN_WIDTH = 68;

// 无渲染内容的分割，用户区别两个连续的按钮，做 round border 的效果
export const DIVIDER_NONE = 'mobileToolbar_tinySeperator';

// 新行
export const NEW_LINE = 'mobileToolbar_newLine';

// 小分割（约 12px）
export const DIVIDER = 'mobileToolbar_divider';

// 小分割（竖向）
export const DIVIDER_VERT = 'mobileToolbar_divider_vert';

// 空白的 icon，用于最后一行的左对齐
export const ALIGN_PLACEHOLDER = 'mobileToolbar_emptyIcon';

export const THREED_TRAMSFORM = 'translate3d(0, 0, 0)';

// 浮出二级菜单的最小左侧距离
export const SUBLINE_MIN_LEFT = 24;
// 浮出二级菜单和底部的 margin
export const SUBLINE_MARGIN_BOTTOM = 8;

// name -> itemKey。移除 [. 空格 -]，转小写
export const itemToKey = (item: MobileToolbarItem) => {
  const name = typeof item === 'object' ? item.name : item;
  return String(name).replace(/ |\.|-/g, '_').toLowerCase();
};

export default {
  DIVIDER,
  DIVIDER_NONE,
  DIVIDER_VERT,
  NEW_LINE,
};
